import React, {useEffect, useState} from "react";
import {Button, Modal} from "antd";
import styles from 'assets/jss/modal'
import consts, {PRIMARY_COLOR} from "../../consts";
import {makeStyles} from "@material-ui/core/styles";
import 'assets/css/modal_repair.css';
import {useTranslation} from "react-i18next";
import FormItem from "../FormItem/FormItem";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faChevronRight} from "@fortawesome/free-solid-svg-icons";
import utils from "../../common/utils";
import {isMobile} from "react-device-detect";
import OTPModal from "../OTP/OTPModal";

const useStyles = makeStyles(styles);

function ModalLogin(props) {
    const classes = useStyles();
    const {visible, handleCancel, handleOk} = props;
    const [loading, setLoading] = useState(false);
    const [phone, setPhone] = useState('');
    const [errorPhone, setErrorPhone] = useState('')
    const {t, i18n} = useTranslation('translation');
    const [otpError, setOTPError] = useState('');
    const [timeOTP, setTimeOTP] = useState(3);
    const [otpShow, setOtpShow] = useState(false);
    const [defaultValue, setDefaultValue] = useState('');

    const sendLogin = (phone, otp) => {
        props.login({
            "isdn": phone,
            "gameCode": consts.GAME_CODE,
            otp,
            "language": i18n.language
        }, () => {
            utils.showNotification('Login', 'Login success')
            // props.cancelModalLogin()
            handleOk()
            setOtpShow(false)
        })
    }

    const onFinish = (event) => {
        event.preventDefault();
        const {phone} = event.target;
        if (phone.value == '') setErrorPhone('Please input your phone!')
        if (!utils.checkPhoneNumber(phone.value)) {
            setErrorPhone('Phone format error')
            return;
        }
        setPhone(phone.value)
        console.log("login-otp")
        // props.cancelModalLogin()
        if (isMobile) getOtpFromSMS();
        props.sendOtp({
            "isdn": phone.value,
            "gameCode": consts.GAME_CODE,
            "language": i18n.language
        }, () => {
            setOtpShow(true)
        })
    };

    const getOtpFromSMS = () => {
        if (!window.OTPCredential) {
            alert('browser not support')
            return;
        }
        window.addEventListener('DOMContentLoaded', e => {
            const abort = new AbortController();

            setTimeout(() => {
                // abort after two minutes
                abort.abort();
            }, 2 * 60 * 1000);
            navigator.credentials.get({
                signal: abort.signal,
                otp: {
                    transport: ["sms"]
                }
            }).then(({code, type}) => {
                console.log(code, 'code');
                if (code && code.length == consts.OTP_LENGTH) {
                    setDefaultValue(code)
                }
                abort.abort();
            });
        })
    }

    return (
        <>
            <Modal title=""
                   destroyOnClose={true}
                   visible={visible}
                   onOk={sendLogin}
                   onCancel={handleCancel}
                   width={220}
                   closable={false}
                   bodyStyle={{borderRadius: 10}}
                   footer={[
                       <form
                           name="login"
                           onSubmit={onFinish}
                           className={classes.formItem}
                       >
                           <FormItem label={'Phone'} require={true} placeholder={t('login-modal-phone-placeholder')}
                                     errorMessage={errorPhone} name={'phone'} reset={() => setErrorPhone('')}/>
                           <input autoComplete={'one-time-code'} defaultValue={defaultValue}/>
                           <Button style={{backgroundColor: PRIMARY_COLOR}} className={classes.btnModal} type={'submit'}
                                   loading={loading}
                                   htmlType="submit">{t('login-modal-btn')} <FontAwesomeIcon
                               icon={faChevronRight} style={{marginLeft: 4,}}/></Button>
                       </form>
                   ]}
            >
                <div style={{textAlign: 'center',}}>
                <span style={{
                    fontSize: 13,
                    lineHeight: '18px',
                    color: '#181725',
                    fontWeight: 600
                }}>{t('login-modal-title')}</span>
                </div>
            </Modal>
            {otpShow ? <OTPModal autoFocus={false} handleOk={(otp) => {
                sendLogin(phone, otp)
            }}
                                 reSend={() => {
                                     props.sendOtp({
                                         "isdn": phone.value,
                                         "gameCode": consts.GAME_CODE,
                                         "language": i18n.language
                                     }, () => {
                                     })
                                 }}
                                 defaultValue={defaultValue}
                                 visible={true}
                                 otpError={otpError}
                                 time={timeOTP}
                                 handleCancel={() => {
                                     setOtpShow(false)
                                 }}/> : <div/>}
        </>
    )
}

export default ModalLogin;
