import React, {Component, useRef} from "react";
import styles from "../../assets/jss/banner";
import withStyles from "@material-ui/core/styles/withStyles";
import Slider from "react-slick";
import one from 'assets/img/banner/one.png';
import two from 'assets/img/banner/two.png';
import three from 'assets/img/banner/three.jpg';
import _ from 'lodash';

const defaultBanner = [
    one,
    two,
    three,
]

class Banner extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        const
            settings = {
                dots: false,
                infinite: true,
                slidesToShow: 1,
                slidesToScroll: 1,
                autoplay: true,
                autoplaySpeed: 1700
            };
        const {classes} = this.props;
        const banners = this.props.banners || defaultBanner;

        return (

            <div className={classes.container}>
                <Slider ref={slider => (this.slider = slider)} {...settings}>
                    {_.map(banners, e => <div style={{height: 300}}>
                        <img src={e} className={classes.image}/>
                    </div>)}
                </Slider>
            </div>
        )
    }
}

export default withStyles(styles)(Banner);
